/* eslint-disable jsx-a11y/anchor-is-valid */
import {FC} from 'react'
import {useLayout} from '../../../_metronic/layout/core'

const Footer: FC = () => {
  const {classes} = useLayout()
  return (
    <div className='footer py-4 d-flex flex-lg-column' id='kt_footer'>
      {/* begin::Container */}
      <div
        className={`${classes.footerContainer} d-flex flex-column flex-md-row align-items-center justify-content-between`}
      >
        {/* begin::Copyright */}
        <div className='text-dark order-2 order-md-1'>
          <span className='text-muted fw-bold me-2'>{new Date().getFullYear()} &copy;</span>
          <a href='#' className='text-gray-800 text-hover-primary'>
            MetaMusic Inc.
          </a>
        </div>
        {/* end::Copyright */}

        {/* begin::Nav */}
        <ul className='menu menu-gray-600 menu-hover-primary fw-bold order-1'>
          {/* <li className='menu-item'>
            <a href={`${process.env.REACT_APP_FOOTER_CONTACT_URL}`} className='menu-link pe-0 pe-2'>
              Privacy Policy
            </a>
          </li> */}
          <li className='my-auto me-3'>
            <a href={`${process.env.REACT_APP_DOCUMENTS_URL}/themusician_Terms of Service_0.1.pdf`} className='text-dark pe-0 pe-2'>
              Terms of Service
            </a>
          </li>
          <div className="align-right">
            <div className="vc_column-inner">
              <div className="wpb_wrapper">
                  <div className="vc_btn3-container vc_btn3-inline">
                      <a style={{ color: '#262423' }} className="vc_general vc_btn3 vc_btn3-size-lg vc_btn3-shape-rounded vc_btn3-style-custom vc_btn3-o-empty vc_btn3-icon-left" href="//soundcloud.com" title="" target="_blank">
                          <i className="vc_btn3-icon fab fa-soundcloud"></i>
                          <span className="vc_btn3-placeholder">&nbsp;</span>
                      </a>
                  </div>
                  <div className="vc_btn3-container vc_btn3-inline mx-2">
                      <a style={{ color: '#262423' }} className="vc_general vc_btn3 vc_btn3-size-lg vc_btn3-shape-rounded vc_btn3-style-custom vc_btn3-o-empty vc_btn3-icon-left" href="//facebook.com" title="" target="_blank">
                          <i className="vc_btn3-icon fab fa-facebook-f"></i>
                          <span className="vc_btn3-placeholder">&nbsp;</span>
                      </a>
                  </div>
                  <div className="vc_btn3-container vc_btn3-inline">
                      <a style={{ color: '#262423' }} className="vc_general vc_btn3 vc_btn3-size-lg vc_btn3-shape-rounded vc_btn3-style-custom vc_btn3-o-empty vc_btn3-icon-left" href="//twitter.com" title="" target="_blank">
                          <i className="vc_btn3-icon fab fa-twitter"></i>
                          <span className="vc_btn3-placeholder">&nbsp;</span>
                      </a>
                  </div>
                  <div className="vc_btn3-container vc_btn3-inline mx-2">
                      <a style={{ color: '#262423' }} className="vc_general vc_btn3 vc_btn3-size-lg vc_btn3-shape-rounded vc_btn3-style-custom vc_btn3-o-empty vc_btn3-icon-left" href="//twitter.com" title="" target="_blank">
                          <i className="vc_btn3-icon fab fa-instagram"></i>
                          <span className="vc_btn3-placeholder">&nbsp;</span>
                      </a>
                  </div>
                  <div className="vc_btn3-container vc_btn3-inline">
                      <a style={{ color: '#262423' }} className="vc_general vc_btn3 vc_btn3-size-lg vc_btn3-shape-rounded vc_btn3-style-custom vc_btn3-o-empty vc_btn3-icon-left" href="//twitter.com" title="" target="_blank">
                          <i className="vc_btn3-icon fab fa-youtube"></i>
                          <span className="vc_btn3-placeholder">&nbsp;</span>
                      </a>
                  </div>
              </div>
            </div>
          </div>
        </ul>
        {/* end::Nav */}
      </div>
      {/* end::Container */}
    </div>
  )
}

export {Footer}
