const OrderListPagination: FC<Props> = ({ totalNumberOfPages, pageList, fetchNextData, selectedPage }) => {

    return (
        <div className='row'>
            <div className='d-flex'>
                {selectedPage > 1 && 
                    <button 
                        style={{ 
                            border: '0.1px solid #009EF7',
                            background: '#ffd600',
                            borderRadius: '5px',
                            color: '#000',
                            height: '30px',
                            fontSize: '15px'
                        }}
                        onClick={() => fetchNextData(selectedPage - 1)}
                        disabled={selectedPage == 1}
                    >
                        Prev
                    </button>
                }
                <div className='d-flex my-auto'>
                    <span className='ms-2'>{selectedPage}</span>
                    <span className='mx-3'>Of</span>
                    <span className='me-2'>{totalNumberOfPages}</span>
                </div>
                {selectedPage < totalNumberOfPages && 
                    <button 
                        style={{ 
                            border: '0.1px solid #009EF7',
                            background: '#ffd600',
                            borderRadius: '5px',
                            color: '#000',
                            height: '30px',
                            fontSize: '15px'
                        }}
                        onClick={() => fetchNextData(selectedPage + 1)}
                        disabled={selectedPage == totalNumberOfPages}
                    >
                        Next
                    </button>
                }
            </div>
        </div>
    )
}

export {OrderListPagination}
